	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.flickr.com/photos/papascott/458505383/\" title=\"Photo Sharing\"><img src=\"http://farm1.static.flickr.com/211/458505383_869cae7c37.jpg\" width=\"500\" height=\"375\" alt=\"IMG_1964\" /></a></p>\n<p>As you can see, we've started setting up our business. </p>\n<p>In the upper left you can see the two founders. They are stubborn as donkeys, and have already dressed warm (*haben sich warm angezogen*) in preparation for the tough times ahead. Between them is a crystal ball with a vision of the future.</p>\n<p>To their right is a device for cutting costs. If twisted just right it can also be used to cut corners. On the shelf beneath them we are trying to bring light into the darkness. </p>\n<p>(Hmm, somehow this seemed much funnier when we were cleaning out the shelf. But the two binders to the left are really our first business files.)</p>","fields":{"title":"Founding the Business","date":"14 Apr 2007","url":"https://papascott.de/2007-04-14-founding-the-business/"}}},"pathContext":{"slug":"/2007-04-14-founding-the-business/","prev":{"fields":{"title":"Cem's Back","date":"14 Apr 2007","slug":"/2007-04-14-cems-back/"}},"next":{"fields":{"title":"Euro License","date":"15 Apr 2007","slug":"/2007-04-15-euro-license/"}}}}

